.checkbox {
  position: relative;
  overflow: hidden;
  width: 26px;
  height: 26px;
  cursor: pointer;
  flex-shrink: 0;
}

.checkbox__input {
  display: none;
}

.checkbox__inner {
  display: inline-block;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 1px solid #626262;
  background: transparent no-repeat center;
}

